

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  color: inherit;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input, textarea {
  outline: none;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
}

:root {
  --brand-color: #4f2ee8;
  --text-color: #111;
  --subtitle-color: #b4afaf;
  --categories-rgba-color: rgba(17, 17, 17, .6);
  --gray-button-color: #f3f3f3;
  --gray-rgba-color: rgba(246, 246, 246, .5);
  --background-color: #f6f6f6;
  --white-color: #fff;
  --yelow-color: #eac645;
}

.container {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

.main-title {
  letter-spacing: -1.92px;
  font-size: 48px;
  font-weight: 700;
  line-height: 1.08;
}

.title {
  text-transform: uppercase;
  letter-spacing: -.64px;
  color: var(--subtitle-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 1.12;
}

.subtitle {
  letter-spacing: -.48px;
  color: var(--subtitle-color);
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.16;
}

.shopping-list__subtitle {
  letter-spacing: .36px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.16;
}

.text {
  letter-spacing: -.56px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}

.categories__title {
  color: var(--categories-rgba-color);
  letter-spacing: -.36px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.33;
  transition: color .25s linear;
}

.categories__title:hover, .categories__title:focus {
  color: var(--brand-color);
  text-transform: uppercase;
  font-weight: 700;
}

.button {
  color: var(--text-color);
  border: 2px solid var(--brand-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 40px;
  transition: background-color .25s linear, color .25s linear;
}

.button:hover, .button:focus {
  color: var(--white-color);
  background-color: var(--brand-color);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(0, 0, 0, .5);
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 500px;
  z-index: 30;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

.menu-hamburger {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  padding: 20px 0 14px;
}

.icon-menu-hamburger {
  width: 28px;
  height: 28px;
  stroke: #000;
  justify-content: center;
  align-items: center;
  display: flex;
}















